import { useMemo } from "react";
import { debounce as Dbs } from "lodash";

function debounce<R = void>(
  fn: (args: any[]) => R,
  delay: number = 200,
  immediate: boolean = false,
  callback?: (result: ReturnType<typeof fn>) => void,
) {
  // 1., 
  let timer: NodeJS.Timeout | null = null;
  let isInvoke = false;

  // 2.
  const _debounce = function (...args: any[]) {
    return new Promise((resolve, reject) => {
      // 
      if (timer) clearTimeout(timer);
      // 
      if (immediate && !isInvoke) {
        // @ts-ignore
        const result = fn.apply(this, args);
        if (callback) callback(result);
        resolve(result);
        isInvoke = true;
        // isInvoke
        timer = setTimeout(() => {
          isInvoke = false;
          timer = null;
        }, delay);
      } else {
        isInvoke = true;
        // 
        timer = setTimeout(() => {
          // 
          // @ts-ignore
          const result = fn.apply(this, args);
          if (callback) callback(result);
          resolve(result);
          isInvoke = false;
          timer = null;
        }, delay);
      }
    });
  };

  // 
  _debounce.cancel = function () {
    if (timer) clearTimeout(timer);
    timer = null;
    isInvoke = false;
  };

  return _debounce;
}

const emptyFn = () => {};

export default function useDebounce<R = void>(
  fn: (args: any[]) => R,
  delay: number = 200,
  immediate: boolean = false,
  callback?: (result: ReturnType<typeof fn>) => void
) {
  if (typeof fn !== "function") {
    throw new Error("fn must be a function");
  }
  if (typeof delay !== "number") {
    throw new Error("delay must be a number");
  }
  const debounceFn = useMemo(() => {
    if (delay < 0) {
      return emptyFn as ReturnType<typeof debounce<R>>;
    }
    if (delay === 0) {
      return fn as any as ReturnType<typeof debounce<R>>;
    }
    return debounce(fn, delay, immediate, callback);
  }, [fn, delay, immediate, callback]);

  return debounceFn;
}
